import React, {Component} from 'react'
import './Home.css';
import Markdown from 'react-markdown';

var predefinedText = "\n## Welcome to Markdown live previewer.\n\n### Type your markdown syntax here and see the live preview\n\nIt's very easy to make some words **bold** and other words *italic* with Markdown.\nSometimes you want numbered lists\n\n1. One\n2. Two\n3. Three\n\nSometimes you want bullet points:\n\n* Start a line with a star\n* Profit!";

class Home extends Component{
	constructor(props){
		super(props);
		this.state = {
			markdown : predefinedText
		};

		this.updateMarkdown = this.updateMarkdown.bind(this);
	}

	updateMarkdown(e){
		this.setState({
			markdown : e.target.value
		});
		console.log(this.state.markdown);
	}

	render(){
		return (
			<>
			<div className="homeBody">
				<div className ="columnsContainer">
					<div className="rightCol">
						<div className="headingContainer header">
							Markdown Editor
						</div>
						<textarea
					      className="form-input" 
					      onChange={this.updateMarkdown}
					      placeholder={"Type here"} 
					    >{predefinedText}</textarea>
					</div>	
					
					<div className="leftCol">
						<div className="headingContainer header">
							Preview
						</div>
						<Markdown 
							className="markdownCol"
                  			escapeHtml={true}
                    		source={this.state.markdown} 
                		/>
					</div>				
				</div>
			</div>
			</>
		);
	}
}

export default Home;